.category-gallery {
  min-height: 100vh;
  padding: 120px 2rem 60px;
  background: #000000;
}

.category-gallery-container {
  max-width: 1400px;
  margin: 0 auto;
}

.back-button {
  padding: 0.7rem 1.5rem;
  background: #1a1a1a;
  color: #ffffff;
  border: 2px solid #ea6666;
  border-radius: 25px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 2rem;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}

.back-button:hover {
  background: #667eea;
  color: #fff;
  transform: translateX(-5px);
}

.category-gallery-header {
  text-align: center;
  margin-bottom: 3rem;
}

.category-gallery-title {
  font-size: 3.5rem;
  color: #ffffff;
  margin-bottom: 0.5rem;
  font-weight: 700;
  text-transform: capitalize;
}

.category-gallery-count {
  font-size: 1.2rem;
  color: #aaaaaa;
  font-weight: 500;
}

.category-gallery-grid {
  column-count: 3;
  column-gap: 2rem;
}

.gallery-item {
  position: relative;
  overflow: hidden;
  border-radius: 0;
  cursor: pointer;
  animation: fadeIn 0.6s ease forwards;
  opacity: 0;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  background: #000;
  margin-bottom: 2rem;
  break-inside: avoid;
  display: inline-block;
  width: 100%;
}

.gallery-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
}

.gallery-item img {
  width: 100%;
  height: auto;
  display: block;
  transition: transform 0.5s ease;
}

.gallery-item:hover img {
  transform: scale(1.1);
}

.gallery-item-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.8) 0%, transparent 100%);
  display: flex;
  align-items: flex-end;
  padding: 1.5rem;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.gallery-item:hover .gallery-item-overlay {
  opacity: 1;
}

.gallery-item-overlay h3 {
  color: #fff;
  font-size: 1.2rem;
  margin: 0;
  font-weight: 600;
}

/* Lightbox */
.lightbox {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.95);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  padding: 2rem;
  animation: fadeIn 0.3s ease;
}

.lightbox-content {
  position: relative;
  max-width: 90vw;
  max-height: 90vh;
  animation: scaleIn 0.3s ease;
}

.lightbox-content img {
  max-width: 100%;
  max-height: 80vh;
  border-radius: 0;
  box-shadow: 0 10px 50px rgba(0, 0, 0, 0.5);
  object-fit: cover;
}

.lightbox-content h3 {
  color: #fff;
  text-align: center;
  margin-top: 1rem;
  font-size: 1.5rem;
}

.lightbox-close {
  position: absolute;
  top: -40px;
  right: 0;
  background: none;
  border: none;
  color: #fff;
  font-size: 3rem;
  cursor: pointer;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.2s ease;
}

.lightbox-close:hover {
  transform: rotate(90deg);
}

/* Navigation arrows removed - use keyboard arrows or swipe on mobile */

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

@keyframes scaleIn {
  from {
    transform: scale(0.8);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

@media (max-width: 768px) {
  .category-gallery {
    padding: 100px 1rem 40px;
  }

  .category-gallery-title {
    font-size: 2.5rem;
  }

  .category-gallery-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 1rem;
  }
}
